header{
    nav{background-color: cornflowerblue;
        text-align: center;
        padding: 50px;
        a{
            color: azure;
            font-size: 50px;
            margin: 150px;
           
        }
    }
}

main{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top:100px;
    flex-direction: column;
    h1{font-size: 70px;
        margin-bottom:50px ;}
    h2{ text-align: center;}
    .product{ display: flex; 
        justify-content: space-around;
        list-style: none;
        li{ text-align: center;
            div{
                width: 400px;
                height: 400px;
                border-radius: 50%;
                overflow: hidden;
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
            .p1{
                font-size: 20px;
            }
            .p2{
                font-size: 15px;
            }
        }
    }
    
}